
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'


import { mapState, mapGetters } from "vuex";
export default {
directives: {
TransferDom
},
computed: {
...mapGetters(["feesBranch", "branchList", "feesBranchSelect"])
},
created() {

this.$store.dispatch("getBranchList");
},
activated() {
this.$store.dispatch("getFeesBranch", {
time: this.selectedDate.key,
start: this.startDate,
end: this.endDate
});

this.ajaxData = this.branchList;
},
deactivated() {
this.branchs = [];
},
components: {
XHeader,
Group,
Cell,
Popup,
Checker,
CheckerItem,
Datetime,
Selector,

XButton
},
data() {
return {
branchs: [],
isShow: false,
selectedDate: { key: 1, value: "本月" },
dateClass: [
{
key: 1,
value: "本月"
},
{
key: 2,
value: "本季度"
},
{
key: 3,
value: "本年"
}
],
startDate: "",
endDate: "",
ajaxData: [],
selectedData: "",
searchText: ""
};
},
watch: {
startDate: function(val) {
if (val) {
this.selectedDate = {};
}
},
endDate: function(val) {
if (val) {
this.selectedDate = {};
}
},
selectedDate: function(val) {
if (Object.keys(val).length) {
this.startDate = "";
this.endDate = "";
}
},
feesBranch(Value) {
this.branchs = Value;
},
branchList: function(val) {
this.ajaxData = val;
},
feesBranchSelect: function(val) {
this.branchs = val;
}
},
methods: {
changChoose() {
this.isShow = true;
},
hidePopup() {
this.isShow = false;
},
setToday(value) {
let now = new Date();
let cmonth = now.getMonth() + 1;
let day = now.getDate();
if (cmonth < 10) cmonth = "0" + cmonth;
if (day < 10) day = "0" + day;
this.startDate = now.getFullYear() + "-" + cmonth + "-" + day;
},
searchData() {
this.searchText = searchText;
},
choosedData() {
if (!this.selectedDate && !this.startDate && !this.endDate) {
this.$vux.toast.text("请选择时间", "middle");
} else if (this.startDate && !this.endDate) {
this.$vux.toast.text("请选择结束时间", "middle");
} else if (!this.startDate && this.endDate) {
this.$vux.toast.text("请选择开始时间", "middle");
} else {
this.isShow = false;

this.$store.dispatch("getFeesBranchSelect", {
time: this.selectedDate.key,
start: this.startDate,
end: this.endDate,
department_id: this.selectedData
});

this.selectedData = "";

if (Object.keys(this.selectedDate).length) {
window.sessionStorage.removeItem("fee_start");
window.sessionStorage.removeItem("fee_end");
window.sessionStorage.setItem("fee_time", this.selectedDate.key);
} else if (this.startDate && this.endDate) {
window.sessionStorage.removeItem("fee_time");
window.sessionStorage.setItem("fee_start", this.startDate);
window.sessionStorage.setItem("fee_end", this.endDate);
}
}
}
}
};



